export default {
  data() {
    return {
      category: "all",
      categoryList: [
        {
          id: 1,
          label: "all",
        },
        {
          id: 2,
          label: "國片",
        },
        {
          id: 3,
          label: "日本電影",
        },
        {
          id: 4,
          label: "歐美電影",
        },
      ],
      graphicData: [],
    };
  },
  computed: {
    filterGraphicData() {
      return (data) => {
        if (this.category === "all") {
          return data;
        } else {
          return data.filter((item) => item.category === this.category);
        }
      };
    },
  },
  methods: {
    getData() {
      this.axios({
        method: "get",
        url: "https://raw.githubusercontent.com/Rosette-xii/vue-mysite/main/src/assets/static/data.json",
      })
        .then((res) => {
          this.graphicData = res.data.graphicData.reverse();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    filterCategory(item) {
      this.category = item.label;
      this.goTop();
    },
    goTop() {
      window.scrollTo(0, 0);
    },
  },
  mounted() {
    this.getData();
    this.goTop();
  },
};
